@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:color';


.header{
    padding-top: 5rem;

    &__grid{
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
        }
    }
}